

.body-1{
    background-color: black;
     padding: 10px;
     display: flex;
     align-items: center;
     justify-content: center;
     flex-direction: column;
     margin: 0;
}
h1{
    color: #ea5b0c;
}

h2 {
    color: white;
}
#title{
    width: 100%;
    height: 80vh;
    padding: 10px;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-color: rgb(28, 28, 28);
    border-radius: 10px;
   
}

#title span{
    color: white;
}

#cards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
   justify-content: center;
   background-color: rgb(28, 28, 28);
   border-radius: 10px;
   
}

#cards img{
    width: 30vh;
    height: 30vh;
}
#cards .card{
    width: 30vh;
    height: auto;
   background-color: rgb(238, 238, 238);
   
   padding: 10px;

   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   margin: 10px;
  
  
}
.card strong{
    width: 100%;
    border-radius: 5px;
    background-color: black;
    color: white;
    text-align: center;
}
